#search{
  margin-top: 50px;
  padding: 0px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: 'Lato' !important;
}

.search-box{
  position: relative;
}

.input-search{
  height: 50px;
  width: 50px;
  border: 1px solid black;
  padding: 10px;
  font-size: 18px;
  letter-spacing: 2px;
  outline: none;
  border-radius: 15px;
  transition: all .5s ease-in-out;
  background-color: rgba(0, 0, 0, 0.475);
  padding-right: 40px;
  color:white;
}

.input-search::placeholder{
  color: white;
  font-size: 18px;
  letter-spacing: 2px;
  border-radius: 15px;
  font-weight: 100;
}

.btn-search{
  width: 50px;
  height: 50px;
  border-style: none;
  font-size: 20px;
  font-weight: bold;
  outline: none;
  cursor: pointer;
  border-radius: 50%;
  position: absolute;
  right: 0px;
  color:white;
  background-color:transparent;
  pointer-events: painted;
  opacity: 100% !important;
}

.btn-search:focus ~ .input-search{
  width: 300px;
  background-color: rgba(0, 0, 0, 0.529);
  transition: all 500ms cubic-bezier(0, 0.110, 0.35, 2);
}

.input-search:focus{
  width: 300px;
  border-radius: 15px;
  background-color: rgba(0, 0, 0, 0.523);
  transition: all 500ms cubic-bezier(0, 0.110, 0.35, 2);
}

*{
  box-sizing: border-box;
}

.spinner {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  border: 16px solid rgba(0, 0, 0, 0.639);
  border-top: 16px solid #CC5500;
  animation: spin 2s linear infinite;
}

@keyframes spin{
  0%{
   transform: rotate(0deg);
  }
  100%{
   transform: rotate(360deg);
  }
}

.error {
  display:flex;
  width: 250px;
  height: 30px;
  margin: 0 auto;
  text-align: center;
  font-size: 20px;
  background-color: rgba(0, 0, 0, 0.516);
  color: white;
  justify-content: center;
  font-family: arial;
  border-radius:15px;
  justify-content: center;
  align-items: center;
  margin-top: 15px;
}

.invisible {
visibility: hidden;
}