body {
  background: url("src/images/background1.png") no-repeat center center fixed;
  background-size: cover;
  width: 100vw;
  height: 100%;
  background-repeat: no-repeat;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
}

.hidden {
  display:none !important;
}

#logo {
  top: 40%;
  position: relative;
  width: 200px;
  height: 200px;
}

.header-container {
  display: flex;
  height: 80px;
  justify-content: space-evenly;
}

.page-container {
  padding-top: 40px;
}

.icon-container {
  width:5%;
  margin: 10px;
}

.header-right {
  width: 33.33%;
  justify-content: right;
  display: flex;
  margin-right: 10px;
  align-items: center;
  padding: 20px;
}

.header-left {
  width: 33.33%;
  display: flex;
  margin-right: 10px;
  /* align-items: center; */
  padding: 20px;
  vertical-align: middle;
  text-align: center;
}

#main-search {
  display: block;
  justify-content: center;
}

.header-center {
  width: 33.33%;
  display: flex;
  justify-content: center;
  align-content: center;
  align-items: center;
}

#player-view {
  display: flex;
  justify-content: center;
  width: 100%;
  flex-wrap: wrap;
  margin-top: 50px;
}

.chart {
  margin: auto;
  margin-top: 20px;
  display:flex;
  position: relative;
  justify-content: center;
  width: 60%;
  background-color: rgba(0, 0, 0, 0.653);
  border-radius: 30px;
}

#myChart {
  display: flex;
  width: 70%;
  justify-content: center;
}

a:visited {
  text-decoration: none;
  color: black;
}

a {
  text-decoration: none;
}

/* a:hover {
  text-decoration: none;
  color:yellow;
} */

.remove-button {
  position: absolute;
  left: 98.5%;
  top: 2%;
  cursor: pointer;
  font-family: arial;
  opacity: 0.5;
}

.remove-button:hover {
  opacity: 1;
}

.dropContainer {
  vertical-align: middle;
  text-align: center;
}